let navWrap = document.querySelector('.nav-wrapper');
let nav = document.querySelector('.header__nav');
let logo = document.querySelector('.nav__logo');
let navItem = document.querySelectorAll('.header__nav__item');
if (document.documentElement.clientWidth > 1110) {
window.addEventListener('scroll', function () {
  if (pageYOffset >= 180) {
    navWrap.style.position = 'fixed';
    nav.style.width = 'max-content';
    logo.style.display = 'inline-block';
    navItem.forEach(item =>  {
      item.style.borderRight = 'none';
    })
  } else {
    navWrap.style.position = 'relative';
    nav.style.width = '100%';
    logo.style.display = 'none';
    navItem.forEach(item =>  {
      item.style.borderRight = '1px solid #efefef';
})
}
});
}
if (document.documentElement.clientWidth < 1110) {
  window.addEventListener('scroll', function () {
    if (pageYOffset >= 400) {
      navWrap.style.position = 'fixed';
      nav.style.width = '100%';
      logo.style.display = 'block';
    
    } else {
      navWrap.style.position = 'relative';
      logo.style.display = 'none';
    }
  });
}
